@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Titillium Web', sans-serif;
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
    background-color: #fbba00;
}

em {
    text-decoration: underline;
    -webkit-text-decoration-color: #e82f7d;
            text-decoration-color: #e82f7d;
    font-style: normal;
}

.MuiInputBase-root {
    font-family: 'Titillium Web', sans-serif !important;
    font-size: calc(10px + 2vmin) !important;
}

.Flower {
    position: absolute;
    width: 80px;
    height: 80px;
    background-image: url(/vaccinipertutti/static/media/logo.edac9da1.png);
    background-size: contain;
    background-repeat: no-repeat;
}

.Cube {
    position: absolute;
    width: 40px;
    height: 40px;
    background-color: #e82f7d;
}

.Cube .MuiSvgIcon-root {
    color: #fbba00;
    cursor: pointer;
}

.Flower.lt {
    top: 0;
    left: 0;
    background-position: -40px -40px;
}

.Flower.lb {
    bottom: 0;
    left: 0;
    background-position: -40px 40px;
}

.Flower.rt {
    top: 0;
    right: 0;
    background-position: 40px -40px;
}

.Flower.rb {
    bottom: 0;
    right: 0;
    background-position: 40px 40px;
}

.Cube.rt {
    top: 0;
    right: 0;
}
    
